import React, { Component } from 'react';
import './App.css';
import MovieRow from './MovieRow.js'
import $ from 'jquery'

class App extends Component {

  constructor(props) {
    super(props)
    this.state = {}
 

  }

  Pretraga(searchTerm) {
    console.log("Pretraga filmova")
    const urlString = "https://api.themoviedb.org/3/search/movie?api_key=1b5adf76a72a13bad99b8fc0c68cb085&query=" + searchTerm
    $.ajax({
      url: urlString,
      success: (searchResults) => {
        console.log("Uspesna pretraga")
        const results = searchResults.results

        var movieRows = []

        results.forEach((movie) => {
          movie.poster_src = "https://image.tmdb.org/t/p/w185" + movie.poster_path
          const movieRow = <MovieRow key={movie.id} movie={movie}/>
          movieRows.push(movieRow)
        })

        this.setState({rows: movieRows})
      },
      error: (xhr, status, err) => {
        console.error("Neuspesna pretraga")
      }
    })
  }

  searchChangeHandler(event) {
    console.log(event.target.value)
    const boundObject = this
    const searchTerm = event.target.value
    boundObject.Pretraga(searchTerm)
  }

  render() {
    return (
      <div>
               
       <h1 style={{textAlign:"center"}}>Pretraga filmova</h1>
      
        <input style={{width: "50%", margin:"auto", display: "block"}} onChange={this.searchChangeHandler.bind(this)} placeholder="Unesi naziv filma"/>
        
        <div style={{width: "50%", margin:"auto", display: "block"}}>
          <h3>Pretraga po:</h3>
    <label><input type="radio" name="radioset" />Naslovu</label>
    <label><input type="radio" name="radioset" />Glumcu</label>
    <label><input type="radio" name="radioset" />Godini</label>
    <label><input type="radio" name="radioset" />Zanru</label>
    <label><input type="radio" name="radioset" />Opisu</label>
</div>

        {this.state.rows}

      </div>
    );
  }
}

export default App;


